/* global console, document, Excel, Office */

import { Configuration, RequestMode } from "../functions/request";
import { CALCULATE_SHEET, CALCULATE_FULL } from "../functions/nonCalls";

const getInputElementById = (document: Document, id: string): HTMLInputElement => {
    return document?.getElementById(id) as HTMLInputElement;
}

const isSelectedById = (document: Document, id: string): boolean => {
    return document?.getElementById(id)?.classList.contains("selected");
}

const makeElementDisapearById = (document: Document, id: string) => {
    if (isElementVisible(document, id)) {
        document?.getElementById(`${id}Label`)?.classList.add("hidden");
        document?.getElementById(id)?.classList.add("hidden");
    }
}

const makeElementReapearById = (document: Document, id: string) => {
    if (!isElementVisible(document, id)) {
        document?.getElementById(`${id}Label`)?.classList.remove("hidden");
        document?.getElementById(id)?.classList.remove("hidden");
    }
}

const isElementVisible = (document: Document, id: string) => {
    return !document?.getElementById(id)?.classList.contains("hidden");
}

const isDatacenter = (document: Document): boolean => {
    const elem = document.getElementById("datacenter") as HTMLInputElement;

    if (!!elem) {
        return elem.checked;
    }

    return false;
}

const updateRadioButtons = (document: Document, datacenter: boolean) => {
    const elem = document.getElementById(datacenter ? "datacenter" : "onPremise") as HTMLInputElement;

    if (!!elem) {
        elem.checked = true;
        elem.dispatchEvent(new Event("change"));
    }
}

const loadFromConfig = (document: Document, configuration: any) => {
    if (configuration) {
        const auth = atob(configuration.auth).split(":");

        if (auth && auth.length > 1) {
            getInputElementById(document, "username").value = auth[0];
            getInputElementById(document, "password").value = auth[1];
        }

        getInputElementById(document, "url").value = configuration.url;
        getInputElementById(document, "port").value = configuration.port;

        if (configuration.mode === RequestMode.Design) {
            if (!document.getElementById("mode").classList.contains("selected")) {
                document.getElementById("mode").classList.add("selected");
            }
        } else {
            if (document.getElementById("mode").classList.contains("selected")) {
                document.getElementById("mode").classList.remove("selected");
            }
        }
    }
}

const saveConfig = (document: Document): Configuration => {
    const username = getInputElementById(document, "username").value;
    const password = getInputElementById(document, "password").value;

    const configuration: Configuration = {
        auth: btoa(`${username}:${password}`),
        url: getInputElementById(document, "url").value,
        port: isElementVisible(document, "port") ? getInputElementById(document, "port").value : "",
        mode: isSelectedById(document, "mode") ? RequestMode.Design : RequestMode.RealData
    };

    localStorage.setItem("configuration", JSON.stringify(configuration));
    Office.context.document.settings.saveAsync();

    return configuration;
}

const parseXML = (xmlString: string): Document => {
    const parser = new DOMParser();
    const xmlDoc = parser.parseFromString(xmlString, "application/xml");

    const parserError = xmlDoc.getElementsByTagName("parsererror");
    if (parserError.length > 0) {
        throw new Error(`Error parsing XML: ${parserError[0].textContent}`);
    }

    return xmlDoc;
};

const getItemFromXML = (xml: Document, tag: string): string | null => {
    const item = xml.getElementsByTagName(tag);
    if (item && item.length > 0) {
        return item[0].innerHTML;
    }
    return null;
}

const loadFromFile = (document: Document) => {
    const fileInput = document.createElement("input");
    fileInput.type = "file";
    fileInput.style.display = "none";

    fileInput.addEventListener("change", (event: Event) => {
        const target = event.target as HTMLInputElement;
        const file = target.files?.[0];

        if (file) {
            const reader = new FileReader();

            reader.onload = (e: ProgressEvent<FileReader>) => {
                const content = e.target?.result;

                if (content) {
                    const xml = parseXML(content.toString());

                    let envType = getItemFromXML(xml, "EnvType");

                    updateRadioButtons(document, envType === null ? true : !!envType);

                    const config: Configuration = {
                        auth: getItemFromXML(xml, "Authentification"),
                        url: getItemFromXML(xml, "URL"),
                        port: !!getItemFromXML(xml, "EnvType") ? "" : getItemFromXML(xml, "Port"),
                        mode: RequestMode.RealData
                    };

                    loadFromConfig(document, config);
                }
            };

            reader.onerror = (e: ProgressEvent<FileReader>) => {
                console.error("Error reading file:", e);
            };

            reader.readAsText(file);
        }
    });

    fileInput.click();
};

const saveToFile = async (config: Configuration, isDatacenter: boolean): Promise<void> => {
    const configToXML = (config: Configuration, isDatacenter: boolean): string => {
        const auth = atob(config.auth).split(":");
        let username: string;
        if (auth && auth.length > 1) {
            username = auth[0];
        }
        return `<?xml version="1.0" ?>
<XMLRPC-Config>
<Credentials>
<Username>${username}</Username>
<Authentification>${config.auth}</Authentification>
</Credentials>
<Server>
<URL>${config.url}</URL>
<Port>${config.port}</Port>
<EnvType>${isDatacenter}</EnvType>
</Server>
</XMLRPC-Config>`
    };

    try {
        const fileHandle = await (window as any).showSaveFilePicker({
            suggestedName: "config.xml",
            types: [
                {
                    description: "XML Files",
                    accept: {
                        "application/xml": [".xml"],
                    },
                },
            ],
        });

        const writableStream = await fileHandle.createWritable();
        const xmlContent = configToXML(config, isDatacenter);
        await writableStream.write(xmlContent);
        await writableStream.close();
    } catch (error) {
        console.error("Error saving file:", error);
    }
};

Office.onReady(() => {
    const radioButtons = document.querySelectorAll("input[name=\"host\"]");
    const labels = document.querySelectorAll("label[for=\"onPremise\"], label[for=\"datacenter\"]");

    radioButtons.forEach(radioButton => {
        radioButton.addEventListener("change", (element): void => {
            if ((element.currentTarget as HTMLInputElement).value === "datacenter") {
                makeElementDisapearById(document, "port");
            } else {
                makeElementReapearById(document, "port");
            }
        });
    });

    labels.forEach(label => {
        label.addEventListener("click", (element): void => {
            const associatedRadio = getInputElementById(document, (element.currentTarget as HTMLElement).getAttribute("for"));
            if (!!associatedRadio) {
                associatedRadio.checked = true;
                associatedRadio.dispatchEvent(new Event("change"));
            }
        });
    });

    document.getElementById("loginForm").addEventListener("submit", async (event): Promise<void> => {
        event.preventDefault();
        saveConfig(document);
    });

    document.getElementById("saveToFile").addEventListener("click", async (event): Promise<void> => {
        event.preventDefault();
        saveToFile(saveConfig(document), isDatacenter(document));
    });

    document.getElementById("calcSheet")!.onclick = CALCULATE_SHEET;
    document.getElementById("calcFull")!.onclick = CALCULATE_FULL;

    document.querySelector(".toggle-button").addEventListener("click", (event): void => {
        (event.currentTarget as HTMLElement).classList.toggle("selected");
    });

    document.getElementById("load")?.addEventListener("click", (): void => {
        loadFromFile(document);
    });

    try {
        const configuration = JSON.parse(localStorage.getItem("configuration"));
        loadFromConfig(document, configuration);
    } catch (e) {
        console.error(e);
    }
});

